import { useWatchlistName } from "@rp/react/tanstack-api/watchlist"
import { Skeleton, Typography } from "@rp/react/web/mui/core"
import { ChatFilterChip } from "./ChatFilterChip"

type Props = {
  watchlistId: string
  onRemove: () => void
  openWatchlistModal: () => void
}
const ChatWatchlistChip = ({ watchlistId, onRemove, openWatchlistModal }: Props) => {
  // Using all watchlists so the data doesn't need to load
  const { name } = useWatchlistName(watchlistId)

  return (
    <ChatFilterChip label="Watchlist (BETA):" onClose={onRemove} onClick={openWatchlistModal}>
      {name === undefined ? (
        <Skeleton width={60} />
      ) : (
        <Typography
          sx={{
            maxWidth: 300,
            overflow: "hidden",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
          }}
        >
          {name}
        </Typography>
      )}
    </ChatFilterChip>
  )
}
export { ChatWatchlistChip }
